.identity{
    padding-top: 800px;
    height: 1600px;
}

.our-identity-container{
    padding-top: 100px;
    margin: 50px;
}

.container {
    text-overflow: "-";
    width: 740px;
}


.container h2{
    font-weight:500;
}

.container h5{
    font-size: 30px;
}

.container p{
    font-size: 25px;
    font-weight: 400;
}

.fingerprint-logo-wallpaper{
    width: 600px;
    position: absolute;
    right: 0px;
    top: 850px;
}

.identity-img{
    width: 80px;
    height: 80px;
}

@media ( max-width: 1700px ) {

    .identity{
        padding-top: 800px;
        height: 1750px;
    }   
}

@media ( max-width: 1500px ) {

    .identity{
        height: 1600px !important;
        padding-top: 750px;
    }

    .container {
        padding: 0px !important;
        margin: 50px !important;
    }

    .huella-logo-wallpaper{
        top: 600px;
        width: 50vw
    }

    .identity-img{
        width: 60px;
        height: 60px;
    }

    .arrow{
        height: 40px;
    }

    .fingerprint-logo-wallpaper{
        width: 500px;
        top: 800px;
    }  

    .container h5{
        font-size: 22px;
    }
    
    .container p{
        font-size: 17px;
        font-weight: 400;
    }
}

@media ( max-width: 1300px ) {
    
    .identity{
        padding-top: 600px;
        height: 1500px !important;
    }
}

@media ( max-width: 1100px ) {

    .identity-img{
        width: 30px;
        height: 30px;
        margin-right: 10px !important;
    }

    .identity{
        flex-direction: column;
        padding-top: 500px;
        height: 1900px !important;
    }

    .container {
        padding: 0px !important;
        width: 80vw;
    }

    .fingerprint-logo-wallpaper{
        width: 700px;
        top: 1200px;
        right: 0px;
    }

    .our-identity-container{
        width: 80vw;
        margin: 50px;
    }

}

@media ( max-width: 1000px ) {

    .identity{
        padding-top: 500px;
        height: 1800px !important;
    }
    .fingerprint-logo-wallpaper{
        top: 1100px;
    }

}

@media ( max-width: 800px ) {

    .identity{
        padding-top: 400px;
        height: 1600px !important;
    }

    .fingerprint-logo-wallpaper{
        width: 600px;
        top: 1000px;
    }

    .our-identity-container{
        width: 90vw;
    }

}

@media ( max-width: 750px ) {

    .fingerprint-logo-wallpaper{
        width: 500px;
        top: 900px;
    }

    .our-identity-container{
        margin: 20px;
    }

    .identity{
        padding-top: 400px;
        height: 1600px !important;
    }
}

@media ( max-width: 500px ) {

    .fingerprint-logo-wallpaper{
        width: 400px;
        top: 800px;
    }

    .our-identity-container{
        margin: 20px;
    }

    .identity{
        padding-top: 250px;
        height: 1400px !important;
    }

    .identity h2{
        text-align: center;
    }
}
