body {
    margin: 0;
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    overflow-x: hidden;
    font-family: 'Roboto', sans-serif;
    list-style: none;
}

body a, body a:hover, body a:focus{
    text-decoration: none;
    color: white;
}

.light-theme .background-theme{
    color: #676c6c;
    fill: #676c6c;
    background-color: #c6c8c8;
}

.dark-theme .background-theme{
    color: #ffffff;
    fill: #ffffff;
    background-color: #7d7d7d;
}

body h1, h2, h3, h4, h5, h6{
    font-family: 'Roboto Slab', serif;
}

body p{
    font-size: 18px;
}

body a{
    font-size: 18px;
}

h1{
    font-size: 2.5rem;
}

.hover:hover{
    color:#037fd6;
}


@media ( max-width: 1500px ) {

    body{
        width: 100vw;
    }

    section{
        width: 100vw;
    }

    body h1{
        font-size: 2.5rem;
    }
    body h2{
        font-size: 2rem;
    }
    body h4{
        font-size: 1.3rem;
    }
    body h6{
        font-size: 1rem;
    }
    body p{
        /* font-size: 1rem; */
    }
    body a{
        font-size: 1rem;
    }

}

@media ( max-width: 800px ) {

    body h1{
        font-size: 2rem;
    }
    body h2{
        /* font-size: 35px; */
    }
    body h4{
        font-size: 1rem;
    }
    body p{
        font-size: 16px;
    }
    body a{
        font-size: 15px;
    }
}

@media ( max-width: 800px ) {

    body p{
        font-size: 14px;
    }

}