.work-areas{
    margin: 0px !important;
    padding: 0px !important;
    display: flex;
    flex-direction: column;
    align-items: center;
    height: 400px;
}

.light-theme .work-areas{
    color: #686d6d;
    background: linear-gradient(to bottom, #c7c9c9, #eeefef) !important;
}

.dark-theme .work-areas{
    color: #000;
    background: linear-gradient(to bottom, #747474, #d1d1d1) !important;
}


.work-area-item{
    display: flex;
    font-weight: bolder;
    text-align: start;
    margin: 10px 50px !important;
}

.work-area-list{
    display: grid;
    grid-template-columns: repeat(3, auto);
    width: 75vw;
}

.work-area-icon{
    width: 30px;
    height: 30px;
    margin-right: 10px;
}

@media ( max-width: 1100px ) {

    .work-area-list{
        width: 90vw;
    }
}

@media ( max-width: 900px ) {

    .work-area-list{
        width: 95vw;
    }
}

@media ( max-width: 800px ) {
    .work-area-item{
        margin: 5px 40px !important;
    }
    .work-area-list{
        width: 100vw;
    }
}

@media ( max-width: 650px ) {

    .work-areas{
        margin: 0px !important;
        padding: 30px 40px !important;
        height: 500px;
    }

    .work-areas h1{
        text-align: center;
    }

    .work-area-list{
        display: grid;
        grid-template-columns: repeat(2, auto);
    }

}

@media ( max-width: 400px ) {
    .work-area-item{
        margin: 10px 20px !important;
    }

    .work-areas{
        height: 580px;
    }
}


@media ( max-width: 350px ) {

    .work-area-list{
        display: grid;
        grid-template-columns: repeat(1, auto);
    }

    .work-area-item{
        margin: 10px 60px !important;
    }

    .work-areas{
        height: 750px;
    }

}