.wsp-icon-container{
    position: sticky;
    top: -60px;
    z-index: 100;
}

.wsp-icon{
    width: 100px;
    height: 100px;
    object-fit: cover;
    position: absolute;
    top: 40vh;
    transition-duration: 1s;
    cursor: pointer;  
    right: 0; 
    transform: translateY(40vh);
}

.wsp-icon:hover{
    filter: brightness(70%);
}


@media (max-width: 1500px) {
    .wsp-icon{
        transform: translateY(40vh);
        right: 10px;
        width: 80px;
        height: 80px;
        top: 43vh;
    }
}

@media (max-width: 800px) {

    .wsp-icon{
        transform: translateY(390px);
        right: 0px; 
        top: 35vh;
    }
}