.clients{
    height: 550px !important;
}

.carousel{
    width:80vw;
    position:relative;
    left: 10vw;
}

.carousel h1{
    text-align: center;
}


.client img{
    max-height: 120px;
}

.client{
    margin: 0px;
}

@media ( max-width: 1500px ) {

    .client img{
        max-height: 100px;
    }
    .clients{
        height: 70vh !important;
    }
}

@media ( max-width: 1200px ) {

    .client img{
        height: 100px;
    }

    .clients{
        height: 70vh !important;
    }

}


@media ( max-width: 800px ) {

    .carousel{
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
    }

    .clients-slider .slick-slider{
        width: 60vw;
    }

    .clients{
        height: 300px !important;
    }

    .client img{
        height: 90px;
    }
}

@media ( max-width: 400px ) {

    .clients{
        height: 250px !important;
    }

}