.navbar1{
    z-index: 100;
    height: 100px;
}

.light-theme .navbar1{
    background-color: #EDEEF0;
    color:#686C6C;
}

.dark-theme .navbar1{
    background-color: #000000;
    color: #ffffff;
}

.navbar2{
    z-index: 100;
    position: sticky;
    top: 0px;
}

.language-darklight{
    margin: 0px 10px;
}

.language{
    font-family: 'Roboto', sans-serif;
    font-size: 35px;
    padding: 0px;
    margin: 15px;
}

.nav-link{
    color: #adadad !important;
}

.nav-link:focus, .nav-link:hover {
    color:#fff !important;
}

.nav-link-active{
    color: #fff !important;
}

.language p {
    font-size: 25px;
    margin: 0px;
    font-weight: bolder;
    cursor: pointer;
}

.sun, .sun-moon-icon{
    border-radius: 100%;
    width: 70px;
    height: 70px;
}

.sun-moon-icon{
    padding: 0px 0px;
}

.moon{
    margin: 15px;
    width: 40px;
    position: absolute;
}

.sertic-logo img{
    width: 250px;
}

.navbar-toggler{
    display: flex;
    margin: 0px 10px ;
    border: none;
}

.navbar-toggler-icon {
    width: 40px;
    color: white;
    cursor: pointer;
}

.show .offcanva-dark{
    background-color: #000000;
}

.show .offcanva-dark a:hover{
    color: #686C6C !important;
    background-color: #fff;
}

.show .offcanva-dark .btn-close{
    background-color: white;
}

.show .offcanva-light{
    background-color: #ffffff;
}

.show .offcanva-light a{
    color: #686C6C !important;
}

.show .offcanva-light a:hover{
    color: #ffffff !important;
    background-color: #686C6C;
}

.offcanvas-header{
    height: 80px;
}

.offcanvas-body{
    padding: 0px 40px;
}

.show{
    margin-left: 20px;
    padding: 0px 0px!important;
    line-height: normal;
}

.offcanvas-backdrop{
    margin-left: 0px;
}

.show a{
    font-size:20px !important;
}

.offcanvas a{
    font-weight:bolder;
    font-size:17px;
    padding: 10px !important;
}

.offcanva-menu-workwithus.show{
    height: 200px;
}
.offcanva-menu-home.show{
    height: 480px;
}

@media ( max-width: 1000px ) {
    
    .language-darklight{
        position: absolute;
        bottom:32px;
        right: 0px;
    }

    .assistance-btn{
        position: absolute;
        right: 20px;
        bottom: 5px;
    }

    .language{
        padding-top: 0px !important ;
        padding-right: 25px;
        padding-left: 0px;
        padding-bottom: 0px;
    }

    .language p{
        font-size: 20px;
    }

    .sun-moon-navlink{
        padding-right: 15px;
        padding-bottom: 20px;
    }

    .sun, 
    .sun-moon-icon{
        width: 40px;
        height: 40px;
    }

    .moon{
        margin: 8px;
        width: 23px;
    }

    .sertic-logo img{
        width: 200px;
    }

}

@media ( max-width: 500px ) {

    .sertic-logo{
        padding: 0px !important;
    }
    .sertic-logo img{
        width: 180px;
    }

    .offcanvas{
        margin-left: 0px;
        height: 450px;
        width: 360px;
        padding: 0px 0px!important;
        line-height: normal;
    }
}

@media ( max-width: 350px ) {
    .sertic-logo img{
        width: 140px;
    }

    .sun, 
    .sun-moon-icon{
        width: 30px;
        height: 30px;
    }

    .moon{
        margin: 6px;
        width: 18px;
    }    

    .language p{
        font-size: 15px;
    }

    .language{
        padding-right: 5px;
    }
}