
.list-members{
    padding-top: 0px;
    width: 50%;
}

.list-members .slick-list{
    height: 100vh;
}

.members p{
    text-align: center;
    font-size: 15px;
    width: 200px !important;
}

.members{
    height: 100%;
    width: 50vw;
}

.member{
    position: relative;
}

.members-name-text{
    font-size: 20px;
    font-family: 'Roboto Slab', serif;
}

.members-name{
    z-index: 100;
    overflow: visible;
}

.size-1 .members-name{
    width:110px;
    transform: rotate(12deg) translateY(35px) translateX(-52px);
}

.size-2 .members-name{
    width: 45px;
    transform: rotate(12deg) translateY(7px) translateX(-45px);
}

.size-3 .members-name{
    width: 20px;
    transform: rotate(17deg) translateY(-8px) translateX(-67px);
}

.size-4 .members-name{
    width: 70px;
    transform: rotate(8deg) translateY(-7px) translateX(-51px);
}


.member-img img{
    margin: 0px;
}

.member-img{
    object-fit: cover;
    border-radius: 100%;
    overflow: hidden;
}

.members p{
    width: 400px;
}

.size-1 .member-img{
    width: 250px;    
    height: 250px;
}

.size-1 .member-img img{
    width: 100%;    
    height: 100%;
}

.size-2 .member-img{
    width: 230px;    
    height: 230px;
}

.size-2 .member-img img{
    width: 100%;    
    height: 100%;
}

.size-3 .member-img{
    width: 200px;    
    height: 200px;
}

.size-3 .member-img img{
    width: 100%;    
    height: 100%;
}

.size-4 .member-img{
    width: 200px;    
    height: 200px;
}

.size-4 .member-img img{
    width: 100%;    
    height: 100%;
}

.size-1{
    top:150px;
}

.size-2-member-1{
    top: 200px;
    right: 80px;
}
.size-2-member-2{
    top: 100px;
    left: 80px;
}

.size-3-member-1{
    top: 300px;
    right: 50px;
}
.size-3-member-2{
    top: 50px;
    right: 40px;
}
.size-3-member-3{
    top: 250px;
    left: 0px;
}

.size-4-member-1{
    top: 50px;
    left: 250px;
}
.size-4-member-2{
    top: 400px;
    left: 150px;
}
.size-4-member-3{
    top: 270px;
    right: 350px;
}
.size-4-member-4{
    top: 170px;
    right: 50px
}



@media ( max-width: 1500px ) {

    .size-1 .members-name{
        width: 25px;
        transform: rotate(12deg) translateY(45px) translateX(-45px);
    }
    
    .size-2 .members-name{
        width: 35px;
        transform: rotate(10deg) translateX(-53px) translateY(23px);
    }
    
    .size-3 .members-name{
        width: 70px;
        transform: rotate(5deg) translateY(-17px) translateX(-53px);
    }
    
    .size-4 .members-name{
        width: 100px;
        transform: rotate(1deg) translateY(-15px) translateX(-50px);
    }

    .list-members{
        margin-top:50px;
    }

    .size-1 .member-img{
        width: 300px;        
        height: 300px;
    }
    
    .size-2 .member-img{
        width: 250px;        
        height: 250px;
    }
    
    .size-3 .member-img{
        width: 180px;        
        height: 180px;
    }

    .size-4 .member-img{
        width: 170px;        
        height: 170px;
    }

    .size-2-member-1{
        top: 200px;
        right: 0px;
    }
    .size-2-member-2{
        top: 100px;
        left: 0px;
    }
    .size-3-member-1{
        top: 200px;
        left: 35px
    }
    .size-3-member-2{
        bottom: 0px;
        right: 15px;
    }
    .size-3-member-3{
        top: 150px;
        left: -25px;
    }

    .size-4-member-1{
        top: 40px;
