.arrow{
        cursor: pointer;
        width: 50px !important;
        height: 50px !important;
}

.light-theme .arrow{
    color: #0074a2 !important;
}

.dark-theme .arrow{
    color: #fff;
}

.slick-next{
    right: -70px;
}

.slick-prev{
    left: -70px;
}

.members-arrow{
    position: absolute;
    right: 0px !important;
}

.arrow-faces{
    position:absolute;
    width: 200px !important;
    height: 80px !important;
    top: 75vh !important;
    right: 0px !important;
}

@media ( max-width: 1200px ) {

    .arrow-faces{
        top: 550px !important;
    }

}

@media ( max-width: 1100px ) {

    .arrow-faces{
        width: 200px !important;
        height: 80px !important;
        top: 600px !important;
        right: -50px !important;
    }
}

@media ( max-width: 800px ) {
    .arrow-faces{
        top: 500px !important;
        height: 70px !important;
        width: 40px !important;
        right: 0px !important;
    }
    .arrow{
        width: 20px;
    }
}

@media ( max-width: 600px ) {
    .arrow-faces{
        top: 450px !important;
        height: 55px !important;
    }
}

@media ( max-width: 500px ) {
    .arrow-faces{
        top: 400px !important;
    }
}