.background-contact{
    z-index: -100;
    height: 1470px;
    transform: translateY(-50px);
    position: absolute; 
    width: 100vw;
}

.background-contact img{
    width: 100%;
    height: 100%;
}

.contact{
    height: 950px;
    color: #676c6c;
    margin-top: 250px;
    padding-top: 30px;
}

@media ( max-width: 1300px ) {

    .background-contact{
        overflow: hidden;
    }

    .background-contact img{
        width: 1300px;
    }
}

@media ( max-width: 1000px ) {

    .contact h3{
        font-size: 20px;
    }

    .contact b{
        font-size: 15px;
    }

    .contact{
        margin-top: 190px;
        height: 980px;
        flex-direction: column;
    }

    .sendemail, .info{
        width: 80vw;
    }

    .sendemail{
        height: 550px;
        margin: 20px 0px;
    }

    .sendemail form{
        height: 550px;
    }

    .map{
        display: none;
    }

    .contact-info{
        padding: 10px 20px !important;
        height: 250px;
        margin: 5px 0px;
    }

    .background-contact img{
        object-fit: cover;
        width: 100vw;
    }
}

@media ( max-width: 800px ) {

    .contact{
        margin-top: 180px;
        height: 1000px;
    }
    .background-contact{
        height: 1400px;
    }
}

@media ( max-width: 800px ) {

    .contact-info{
        height: 350px;
    }

}