.footer1{
    color: white;
    background-color: black;
    padding-top: 50px;
}

.footer1 div div{
    width: 300px;
    margin: 0px !important;
}

.footer1 a{
    margin: 2px 0px;
    font-size: 1.1rem !important;
}

.footer2{
    color: black;
    background-color: #c6c6c6;
    margin: 0px;
    padding: 5px;
}
p{
    padding: 0px !important;
    margin: 0px !important;
}

.footer-logo{
    display: flex;
    justify-content: center;
    align-items: start;
}

.footer-logo img{
    width: 150px;
}

.footer-icons{
    display: flex;
    justify-content: center;
    align-items: center;
}

.footer-redirects{
    padding: 0 20px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-around;
}

.footer-nav{
    display:flex;
    flex-direction: column; 
    padding:20px;
}

@media ( max-width: 800px ) {

    .footer1{
        font-size: 10px;
        padding-top: 0px;
    }

    .footer-logo{
        align-items: center;
    }    

    .footer-logo img{
        width: 100px;
    }

    .footer-icons{
        height: 70px !important;
    }

    .footer-redirects{
        justify-content: center;
    }    

    a{
        margin: 0px 0px;
    }
}

@media ( max-width: 500px ) {

    .footer-nav{
        display: none;
    }

    .footer1 div div{
        width: 200px;
        height: 200px ;
    }

    .footer-logo img{
        width: 150px;
    }

    .footer1 p, .footer1 a{
        font-size: .8rem !important;
    }

}

@media ( max-width: 400px ) {

    .footer1 div div{
        width: 150px;
    }

    .footer1 p, .footer1 a, .footer2 p{
        font-size: .6rem !important;
    }
}