.home{
    height: 815px;
    width: 100vw;
    position: absolute;
    overflow: hidden;
}

.identity-wallpaper-container{
    border-radius: 300px;
    margin: 50px;
    box-shadow: 10px 10px 30px #000;
    width: 105vw;
    height: 1000px;
    transform: rotate(-7deg) translateY(-350px) translateX(-50px);
    overflow: hidden;
}

.identity-wallpaper{
    transform: rotate(7deg) translateY(5vw) translateX(30px);
}


@media ( max-width: 1700px ){

    .identity-wallpaper{
        transform: rotate(7deg) translateY(180px) translateX(40px);
    }
}

@media ( max-width: 1500px ){

    .home{
        height: 760px;
    }

    .identity-wallpaper-container{
        border-radius: 250px;
        transform: rotate(-7deg) translateY(-400px) translateX(-50px);
    }
    .identity-wallpaper{
        transform: rotate(7deg) translateY(290px) translateX(50px);
    }
}

@media ( max-width: 1300px ){
    .identity-wallpaper-container{
        border-radius: 200px;
        transform: rotate(-7deg) translateY(-500px) translateX(-50px);
    }

    .identity-wallpaper{
        transform: rotate(7deg) translateY(370px) translateX(50px);
    }
}

@media ( max-width: 1150px ){
    .identity-wallpaper-container{
        transform: rotate(-7deg) translateY(-520px) translateX(-50px);
        width: 107vw;
    }

    .identity-wallpaper{
        transform: rotate(7deg) translateY(450px) translateX(50px);
    }
}

@media ( max-width: 1000px ){

    .identity-wallpaper-container{
        border-radius: 150px;
        transform: rotate(-7deg) translateY(-590px) translateX(-20px);
    }
    .identity-wallpaper{
        transform: rotate(7deg) translateY(530px) translateX(20px);
    }
}

@media ( max-width: 850px ){

    .identity-wallpaper-container{
        transform: rotate(-7deg) translateY(-615px) translateX(-50px);
        width: 110vw;
    }

    .identity-wallpaper{
        transform: rotate(7deg) translateY(550px) translateX(50px);
    }

}


@media ( max-width: 750px ){
    .identity-wallpaper-container{
        height: 600px;
        width: 110vw;
        transform: rotate(-7deg) translateY(-250px) translateX(-50px);
        border-radius: 80px;
    }

    .identity-wallpaper{
        transform: rotate(7deg) translateY(150px) translateX(0px);
        width: 900px !important;
    }
}

@media ( max-width: 600px ){
    .identity-wallpaper{
        transform: rotate(7deg) translateY(150px) translateX(-200px);
        width: 900px !important;
    }
}

@media ( max-width: 500px ){
    .identity-wallpaper-container{
        height: 500px;
        transform: rotate(-5deg) translateY(-300px) translateX(-50px);
        border-radius: 50px;
    }

    .identity-wallpaper{
        transform: rotate(5deg) translateY(200px) translateX(0px);
        width: 600px !important;
    }
}

@media ( max-width: 400px ){

    .identity-wallpaper{
        transform: rotate(5deg) translateY(200px) translateX(-150px);
    }
}