.workwithus-background{
    z-index: -10;
    position: absolute;
    height: 2000px;
    width: 100vw;
    overflow: hidden;
}

.workwithus-background img{
    height: 1100px;
    width: 100%;
}

.workwithus-title{
    margin: 40px 0px;
}

.workwithus{
    min-height: 70vh;
    margin: 50px;
}

.light-theme .workwithus{
    color: #fff;
}

.dark-theme .workwithus{
    color: #000;
}
    

.workwithus-body{
    display: flex;
    justify-content: space-around;
    width: 100%;
}


.workwithus-section-1{
    width: 50vw;
}

.workwithus-section-1 h3{
    font-size: 20px !important;
    width: 40vw;
}

.workwithus-image img{
    width: 40vw;
}

.light-theme .linkedin-icon-workwithus{
    outline: 5px solid #14739F;
}

.linkedin-icon-workwithus{
    margin: 20px;
    width: 100px;
    height: 100px;
    border-radius: 50%;
}

.linkedin-icon-workwithus img{
    width: 100px;
    height: 100px;
    padding: 20px;
}

.dark-theme .workwithus-linkedin-section{
    color: #fff;
}

.light-theme .workwithus-linkedin-section{
    color: #14739F;
}

.workwithus-linkedin-section{
    height: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    text-align: center;
}

.work-with-us-form{
    color: #676c6c;
    font-weight: bolder;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: space-between;
    padding: 50px 50px;
}

.work-with-us-form-sent p{
    margin: 35px 20px !important;
}

@media ( max-width: 1500px ) or ( max-width: 1500px ) {
    .work-with-us-form-sent p{
        margin: 15px 15px !important;
    }

    .workwithus-background{
        height: 210vh;
    }

    .workwithus-background img{
        height: 1000px;
    }
}

@media ( max-width: 1200px ) or ( max-width: 1200px ) {

    .workwithus-background img{
        width: 1200px;
    }

}


@media ( max-width: 1000px ) or ( max-width: 1000px ) {

    .workwithus-body{
        flex-direction: column;
        align-items: center;
    }
    
    .workwithus-title{
        text-align: center;
    }

    .workwithus-section-1{
        width: 100%;
        padding: 0px 10vw;
    }

    .workwithus-section-1 h3{
        text-align: center;
        width: 100%;
    }

    .workwithus-sendemail{
        height: 740px;
    }

    .workwithus-image{
        width: 100%;
        display: flex;
        justify-content: center;
    }

    .workwithus-image img{
        width: 100%;
    }

    .workwithus-background{
        height: 2000px;
    }

    .workwithus-background img{
        height: 650px;
        width: 100%;
    }
}

@media ( max-width: 550px ) or ( max-width: 550px ) {

    .workwithus{
        margin: 7%;
    }

    .workwithus-section-1{
        padding: 0px;
    }

    .workwithus-background img{
        height: 700px;
        width: 800px;
    }

}
